<template>

<div class="container">
    <router-link to="/"><img alt="logotip" src="@/assets/logo.png"></router-link>

    <h1 class="pb-2">
        Añade una valoración sobre nuestros productos y servicios
    </h1>
        
<!-- Formulario para añadir personas -->

        <section class="form">
            <form action="" class="text-center">
                <input v-model="nombre" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Nombre">
                <input v-model="edad" @keyup.enter="crearPersona" type="text" class="form-control" placeholder="Escribe algo...">
                
                <!-- Botón para añadir -->
                <input @click="crearPersona" type="button" value="Comentar" class="btn btn-outline-secondary">
            </form>
        </section>
        
<!-- Tabla donde se muestran los datos -->
        <section class="data">
            <caption>Valoraciones</caption>
            <table class="table">
                <thead>
                    <tr>
                        <th scope="col">Nombre</th>
                        <th scope="col">Comentario</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(persona, index) in personas" :key="persona.id">
                        
                        <td>
                            <span>
                                
                            </span>
                            <span>
                                
                                <!-- Dato nombre -->
                                {{ persona.nombre }}
                            </span>
                        </td>
                        <td>
                            <span v-if="formActualizar && idActualizar == index">
                                
                                <!-- Formulario para actualizar -->
                                <input v-model="edadActualizar" type="text" class="form-control">
                            </span>
                            <span v-else>
                                <!-- Dato edad -->
                                {{ persona.edad }}
                            </span>
                        </td>
                        <td>
                            
                            <!-- Botón para guardar la información actualizada -->
                            <span v-if="formActualizar && idActualizar == index">
                                <button @click="guardarActualizacion(index)" class="btn btn-success">Guardar</button>
                            </span>
                            <span v-else>
                                <!-- Botón para borrar -->
                                <button @click="borrarPersona(index)" class="btn btn-danger">Borrar</button>
                            </span>
                        </td>
                    </tr>
                </tbody>
            </table>
        </section>
    </div>
</template>




    <script>
    export default {
        data() {
        return {
            // Input nombre
            nombre: '',
            // Input edad
            edad: '',
            // Ver o no ver el formulario de actualizar
            formActualizar: false,
            // La posición de tu lista donde te gustaría actualizar 
            idActualizar: -1,
            // Input nombre dentro del formulario de actualizar
            nombreActualizar: '',
            // Input edad dentro del formulario de actualizar
            edadActualizar: '',
            // Lista de personas
            personas: [] 
         }
    },
        methods: {
            crearPersona: function () {
                
                // Anyadimos a nuestra lista
                this.personas.push({
                    nombre: this.nombre,
                    edad: this.edad
                });
                
                // Vaciamos el formulario de añadir
                this.nombre = '';
                this.edad = '';
            },
              borrarPersona: function (persona_id) {
                
                // Borramos de la lista
                this.personas.splice(persona_id, 1);
            },

        }

    }

</script>

